.home_section {
  padding: 0px 70px;
  height: 100vh;
  max-height: 800px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 1280px;
  margin: auto;
  /* border: 1px solid black; */
}

.home_left {
  flex: 0.4 1;
  margin: auto;
}

.home_img {
  position: relative;
  width: 100%;
  max-width: 341px;
  height: 380px;
  /* border: 1px solid black; */
}

.home_img .cover {
  background: var(--purple-light-1);
  position: absolute;
  width: 100%;
  max-width: 341px;
  height: 410px;
  border-radius: 16px 16px 16px 20px;
  /* border: 1px solid black; */
  display: none;
}

.home_img:hover {
  cursor: pointer;
}

.home_img .cover::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 16px;
  background: var(--purple);
  background-color: #7e74f1 !important;
  right: 0;
  z-index: -1;
  transform: rotate(350deg);
  animation: enter-hero-cover 0.8s cubic-bezier(0.42, 0, 0.12, 1.28);
  transition: all 0.2s;
}

.home_img img {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: var(--purple);
  background-color: #7e74f1 !important;
  right: 0;
  z-index: 1;
  transform: rotate(360deg);
  animation: enter-hero-cover 0.8s cubic-bezier(0.42, 0, 0.12, 1.28);
  transition: all 0.5s;
  border: 10px solid var(--purple);
}

.home_img img {
  position: relative;
  height: 380px;
  width: 380px;
  top: 10px;
  left: 20px;
  /* border-bottom-right-radius: 16px;
  border-top-right-radius: 16px;
  border-top-left-radius: 16px; */
  border-radius: 20%;
  /* border: 1px solid rgb(229, 15, 15); */
}


.home_img:hover .cover::after {
  transform: rotate(353deg);
}

.home_right {
  flex: 0.5 1;
  /* border: 5px solid black; */
  /* height: auto; */
}

.greet {
  font-style: normal;
  font-weight: 500;
  font-size: 12px;
  line-height: 24px;
  letter-spacing: 7.5px;
  text-transform: uppercase;
  padding-left: 32px;
  position: relative;
  color: var(--cinder-light);
  display: inline-block;
}

.greet::after {
  content: "";
  position: absolute;
  left: 0;
  top: 10px;
  width: 16px;
  height: 2px;
  background: var(--light-4);
}

.heading_1 {
  font-style: normal;
  font-weight: 700;
  font-size: 44px;
  line-height: 55px;
  margin: 0 0 32px;
  color: var(--cinder);
}

.heading_1 span {
  color: var(--purple);
}

.social_icons {
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 25px;
}

.social_icons svg {
  cursor: pointer;
  margin-right: 25px;
  color: var(--cinder);
}

.social_icons svg:hover {
  opacity: 0.7;
}

.desc {
  max-width: 538px;
  line-height: 32px;
  margin: 0 0 40px;
  color: var(--cinder);
}

.lcr_buttonContainer {
  margin-top: 2rem;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 50px;
  font-family: "var(--primaryFont)", sans-serif;
  /* border: 5px solid black; */
}

.resumeBtn {
  color: var(--cinder-light);
  border-radius: 30px;
  text-transform: inherit;
  text-decoration: none;
  width: 150px;
  font-size: 1rem;
  font-weight: 700;
  height: 50px;
  font-family: var(--primaryFont);
  border: 0.5px solid var(--cinder);
  transition: 100ms ease-out;
  background-color: transparent;
}

.resumeBtn:hover {
  background-color: var(--purple);
  color: white;
  border: 0.5px solid var(--purple-light-1);
}

.contactBtn {
  background-color: var(--purple);
  color: white;
  border-radius: 30px;
  text-transform: inherit;
  text-decoration: none;
  width: 150px;
  height: 50px;
  font-size: 1rem;
  font-weight: 700;
  font-family: var(--primaryFont);
  transition: 100ms ease-out;
  border: 0px solid;
}

.contactBtn:hover {
  background-color: transparent;
  color: var(--cinder);
  border: 2px solid var(--purple-light-1);
}

@media only screen and (max-width: 1024px) {
  .home_section {
    height: 50vh;
  }
}

@media only screen and (max-width: 968px) {
  .home_section {
    /* flex-wrap: wrap; */
    flex-direction: column;
    height: auto;
    max-height: fit-content;
    justify-content: center;
  }

  .home_img {
    height: 450px;
    width: 450px;
  }
  .home_left {
    flex: 0.5 1;
    padding-top: 110px;
  }

  .home_right {
    flex: auto;
    padding: 50px 50px 10px 50px;
    /* border: 1px solid black; */
  }

  .lcr_buttonContainer {
    margin-top: 1.5rem;
    flex-direction: row;
    align-items: center;
    width: 100%;
    height: 70px;
    align-items: flex-start;
    /* border: 2px solid black; */
  }
}

